<template>
  <div class="data-table-wrapper">
    <table class="page-datatable" v-if="dataList.length != 0">
      <thead>
        <tr>
          <th>
            <span>Meting</span>
          </th>
          <th>
            <span>Datum</span>
          </th>
          <th>
            <span>Gedaan door</span>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="dataEntry in dataList" :key="dataEntry.id" @click="$emit('click', dataEntry.id)">
          <td>
            <span>{{dataEntry.data().type}}, {{dataEntry.data().label}}</span>
          </td>
          <td>
            <span>{{dataEntry.data().date}}</span>
          </td>
          <td>
            <span>{{dataEntry.data().author}}</span>
          </td>
        </tr>
      </tbody>
    </table>
    <p class="page-datatable-disclaimer" v-if="dataList.length == 0">Er zijn geen metingen gevonden.</p>
  </div>
</template>

<script>
export default {
  name: "rooms-data-list",
  props: {
    dataList: {
      type: Array
    }
  }
};
</script>

<style>
</style>



